import React from 'react';
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import { BrowserRouter as Router, Link, Routes, Route } from 'react-router-dom';
import Home from './Home'; 
import ScreenTime from './ScreenTime'; 
import Action1 from './Action1'; 
import LockSettings from './LockSettings';
import Tee from './Tee';
import Share from './Share';

function BasicNavbar() {
  return (
    <Router>
      <Navbar bg="warning" expand="lg">
        <Container>
          <Navbar.Brand as={Link} to="/">Don't touch your phone</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/"></Nav.Link>
              <Nav.Link as={Link} to="/screenTime"> </Nav.Link>
              <Nav.Link as={Link} to="/lockSettings"> </Nav.Link>
              <Nav.Link as={Link} to="/tee"> </Nav.Link>
              <Nav.Link as={Link} to="/share"></Nav.Link>
              <NavDropdown title="" id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/action/1"></NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route exact path="/" element={<Home />} />
        <Route path="/screenTime" element={<ScreenTime />} />
        <Route path="/lockSettings" element={<LockSettings />} />
        <Route path="/tee" element={<Tee />} />
        <Route path="/share" element={<Share />} />
        <Route path="/action/1" element={<Action1 />} />
     
      </Routes>
    </Router>
  );
}

export default BasicNavbar;
