import React, { useState } from "react";
import "./Action1.css";

function Action1() {
  const questions = [
    "    .",
    "    .",
    "   13    .",
    "     .",
    "       .",
    "      .",
    "       .",
    "        .",
    "       .",
    "    .",
  ];

  const [counts, setCounts] = useState(new Array(10).fill(0));

  const handleButtonClick = (index) => {
    const newCounts = [...counts];
    newCounts[index]++;
    setCounts(newCounts);
  };

  const totalClicks = counts.reduce((acc, curr) => acc + curr, 0);

  return (
    <div>
      <div className="survey-container" style={{ backgroundColor: "#ADD8E6" }}>
        <h2>   </h2>
        <p>     </p>
        {questions.map((question, index) => (
          <div key={index} className="question-container">
            <p className="question-text">{`${index + 1}. ${question}`}</p>
            <button
              className="click-button"
              onClick={() => handleButtonClick(index)}
            ></button>
          </div>
        ))}
        <hr />
        
      </div>

      <div className="survey-container" style={{ backgroundColor: "#ADD8E6" }}>
      <p className="total-count">  {totalClicks} : </p>
      <p className="total-count" > 10   </p>
      </div>
    </div>
  );
}

export default Action1;
