import React, { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap'; // Button, BsSquare   
// import { BsSquare } from 'react-icons/bs'; //    

function ScreenTime() {
  const [timeElapsed, setTimeElapsed] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setTimeElapsed((prevTime) => prevTime + 1);
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const formatTime = (time) => {
    const hours = Math.floor(time / 3600);
    const minutes = Math.floor((time % 3600) / 60);
    const seconds = time % 60;

    return `${hours > 0 ? hours + ' ' : ''}${minutes > 0 ? minutes + ' ' : ''}${seconds}`;
  };

  return (
    <div className="d-flex justify-content-center align-items-center" 
    style={{ height: '100vh', backgroundColor: '#ADD8E6'  }}>
      <Container>
        <Row className="justify-content-center">
          <Col md={6} className="text-center">
            <div style={{ borderRadius: '20px', padding: '20px', background: '#f0f0f0', display: 'inline-block' }}>
              <h2 style={{ fontSize: '3rem' }}> </h2>
              <p style={{ fontSize: '2rem' }}>{formatTime(timeElapsed)}</p>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default ScreenTime;
