import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Form, Button, Alert } from 'react-bootstrap';

function LockSettings() {
  const defaultTime = 0; //  
  const [inputTime, setInputTime] = useState(defaultTime);
  const [elapsedTime, setElapsedTime] = useState(0);
  const [alertShown, setAlertShown] = useState(false);
  const [timerStarted, setTimerStarted] = useState(false);

  const [showTimerStartAlert, setShowTimerStartAlert] = useState(false);

  const handleInputChange = (event) => {
    const time = event.target.value;
    setInputTime(time);
    setElapsedTime(0);
    setAlertShown(false);
    setShowTimerStartAlert(false);
  };

  const handleStartTimer = () => {
    if (inputTime > 0) {
      setTimerStarted(true);
      setShowTimerStartAlert(false);
    } else {
      setShowTimerStartAlert(true);
    }
  };

  useEffect(() => {
    let timer = null;

    if (timerStarted && elapsedTime < inputTime) {
      timer = setTimeout(() => {
        setElapsedTime((prevTime) => prevTime + 1);
      }, 1000);
    }

    if (elapsedTime >= inputTime && inputTime > 0) {
      setAlertShown(true);
      setTimerStarted(false);
    }

    return () => clearTimeout(timer);
  }, [elapsedTime, inputTime, timerStarted]);

  return (
    <Container
      className="d-flex justify-content-center align-items-center"
      style={{
        height: '100vh',
        background: 'linear-gradient(to bottom right, #ff85a2, #ffb378)',
        color: 'white'
      }}
    >
      <Row>
        <Col md={6} className="text-center">
          <div className="rounded border border-3 border-white p-4">
            <h2> </h2>
            <Form>
              <Form.Group className="mb-3">
                <Form.Control
                  type="number"
                  placeholder="  ()"
                  value={inputTime}
                  onChange={handleInputChange}
                />
              </Form.Group>
              <Button variant="light" onClick={handleStartTimer}> </Button>
              {showTimerStartAlert && (
                <Alert variant="warning" className="mt-2">
                    .
                </Alert>
              )}
            </Form>
            <p> : {inputTime - elapsedTime > 0 ? inputTime - elapsedTime : 0}</p>
            {alertShown && (
              <Alert variant="danger">
                  .
              </Alert>
            )}
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default LockSettings;
